@import url('https://fonts.googleapis.com/css2?family=Lato&family=Poppins:wght@300;400;500;600&display=swap');

/*Tailwind*/
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {

    --color-bg:  rgb(38,63,115);
    --color-bg-gradient: linear-gradient(45deg, rgba(38,63,115,1) 0%, rgba(100,86,191,1) 100%);
    --color-primary: #4480A6;
    --color-text: #94E1F2;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 90%;
    --transition: all 400ms ease;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;


    background: rgb(38,63,115);
    /*background: linear-gradient(45deg, rgba(38,63,115,1) 0%, rgba(100,86,191,1) 100%);*/
    color: var(--color-text);
    line-height: 1.7;
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: 100vh;
}

section > h2, section > h5{
    text-align: center;
    color: var(--color-text);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-text);
}

a {
    color: var(--color-primary);
    transition: var(transition);
}

a:hover {
    color: white;
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: white;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/*Tablets media query*/
@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/*mobile phones media query*/
@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-ms);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}